
.movies{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top:70px ;
    width: 80%;
}

.movies .movie{
    width: 45%;   
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items : flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px ;
    color: #adaeb9;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);

}

.movie img{
    position: relative;
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year{
    margin: 0;
    font-weight: 300;
}

.movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;

    color: #2c2c2c;
}

.movie .moive__genres{
    list-style: none;
    margin: 0;
    padding:0;
    display: flex;
    margin: 5px 0px;
}

.moive__genres li{
    margin-right: 10px;
    font-size: 14px;
}

